import React, { useEffect, useState } from "react";
import { withRouter } from "react-router-dom";

import style from './css/main/Service.module.css';

function Service() {
    function toSelect(e) {
        e.preventDefault();
        window.location.replace('/service-select');
    }

    function toIndex(e) {
        e.preventDefault();
        window.location.replace('/');
    }


    return (
        <div id="wrap" className={style.contentwrap}>
            <div className={style.content} id={style.service}>
                <p>, {localStorage.getItem('userName')}<br />
                    <span>SEAS  '  ?</span>
                </p>
                <div className={style.buttonwrap}>
                    <button type="button" className={style.btn1} onClick={toSelect}>, </button>
                    <button type="button" className={style.btn2} onClick={toIndex}>, </button>
                </div>
            </div>
        </div>
    );
}

export default withRouter(Service);